import React from 'react';
import ProjectPreview from "./ProjectPreview";
import {Container, Col, Row, Button} from "react-bootstrap";
import {Fade} from 'react-reveal';
import { Link as ScrollLink, Element } from 'react-scroll'


function Home() {

    const huskyMapDesc: string = "Pathfinding application for Seattle area made with Java with MapBox and OpenStreetMap APIs";
    const searchEngineDesc: string = "Multithreaded Mini-Search Engine built with C/C++";
    const pongDesc: string = "The game, Pong, made with Java's Swing library";


    return (
        <>
            <Container style={{marginTop: "17em", marginBottom: "17em"}}>
                <Col className="text-center">
                    <h1 style={{fontSize: "4em", fontFamily: "'Fredoka One', cursive", color: "#6cb360"}}> Ronald William Widjaja </h1>
                    <p style={{fontSize: "40px"}}> Software Developer</p>
                </Col>
            </Container>
            <Container style={{marginBottom: "3em"}}>
                <h1 className="text-center">Projects</h1>
                <Row className="justify-content-center">
                    <ScrollLink to="huskymaps" smooth={true} duration={500}>
                        <Button variant="outline-success" style={{margin: "5px", width: "200px"}}>
                            HuskyMaps
                        </Button>
                    </ScrollLink>
                    <ScrollLink to="searchengine" smooth={true} duration={500}>
                        <Button variant="outline-success" style={{margin: "5px",width: "200px"}}>
                            Mini Search-Engine
                        </Button>
                    </ScrollLink>
                    <ScrollLink to="pong" smooth={true} duration={500}>
                        <Button variant="outline-success" style={{margin: "5px",width: "200px"}}>
                            Pong
                        </Button>
                    </ScrollLink>
                </Row>
            </Container>
            <Fade delay={200}>
                <hr style={{borderWidth: "5px", marginTop: "20em", paddingBottom: "0px", marginBottom:"0px"}}/>
                <Container style={{backgroundImage: `url(assets/images/huskymaps/huskymaps.jpg)`, backgroundPosition: "center"}} fluid>
                        <Element name="huskymaps">
                            <ProjectPreview heading="HuskyMaps" description={huskyMapDesc} pathToPage={"/huskymaps"}/>
                        </Element>
                </Container>
                <hr style={{borderWidth: "5px", padding: "0px", margin:"0px"}}/>
                <Container style={{backgroundImage: `url(assets/images/searchengine/333gle.jpg)`, backgroundPosition: "center"}} fluid>
                        <Element name="searchengine">
                            <ProjectPreview heading="Mini Search-Engine" description={searchEngineDesc} pathToPage={"/searchengine"}/>
                        </Element>
                </Container>
                <hr style={{borderWidth: "5px", padding: "0px", margin:"0px"}}/>
                <Container style={{backgroundImage: `url(assets/images/pong/pong.jpg)`, backgroundPosition: "center"}} fluid>
                    <Element name="pong">
                        <ProjectPreview heading="Pong" description={pongDesc} pathToPage={"/pong"}/>
                    </Element>
                </Container>
            </Fade>
        </>
    );
}

export default Home;