import React from 'react';
import {Container, Nav, Navbar} from "react-bootstrap";
import './App.css';

function NavigationBar() {

    return (
        <Container>
            <Navbar fixed="top" collapseOnSelect expand="md" variant="light" bg="white">
                <Navbar.Toggle/>
                <Navbar.Collapse>
                    <Nav className="ml-auto">
                        <Nav.Link style={{marginLeft: "20px", marginRight: "20px", color: "#0d4d00"}} href="/">Home</Nav.Link>
                        <Nav.Link style={{marginLeft: "20px", marginRight: "20px", color: "#0d4d00"}} href="/about">About</Nav.Link>
                        <Nav.Link style={{marginLeft: "20px", marginRight: "20px", color: "#0d4d00"}} href="/resume">Resume</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </Container>
    );
}
export default NavigationBar;