import React from 'react';
import {Container, Button} from "react-bootstrap";

interface ProjectPreviewProps {
    description: string;
    heading: string;
    pathToPage: string;
}

function ProjectPreview(props: ProjectPreviewProps) {
    return (
        <>
            <Container style={{paddingTop: "20em", paddingBottom: "20em"}}>
                <h1 className="text-left" style={{fontSize: "60px"}}>{props.heading}</h1>
                <div>
                    <h5>{props.description}</h5>
                </div>
                <Button href={props.pathToPage} variant="success">
                    More
                </Button>
            </Container>
        </>
    );
}

export default ProjectPreview;