import React, {Component} from 'react';
import {Col, Container, Image, Row} from "react-bootstrap";

interface MemoriesProps {
    picturePath: string;
    description: string;
    alignLeft: boolean;
}

class Memories extends Component<MemoriesProps, {}> {

    render() {
        return (
            <Container>
                <Row className="show-grid">
                    {this.props.picturePath === ""
                        ? null
                        : <Col xs={{order: this.props.alignLeft ? -1 : 1}}>
                            <div className="text-center">
                                <Image width="50%" src={this.props.picturePath} fluid rounded/>
                            </div>
                        </Col>}
                    <Col>
                        <div style={{fontSize: "calc(0.5vw + 0.5vh + 1vmin)"}}> {/*Responsive Text*/}
                            <p>
                                {this.props.description}
                            </p>
                        </div>
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default Memories;