import React from 'react';
import {Col, Container, Image, ListGroup, Row, Tab, Tabs} from "react-bootstrap";

function HuskyMaps() {
    return(
        <>
            <Container style={{marginTop: "5em"}}>
                <Row>
                    <Col lg xl={{ span: 4, offset: 1 }}>
                        <h1 style={{fontSize: "60px"}}> Husky Maps </h1>
                        <ListGroup>
                            <ListGroup.Item variant="warning"><b>Part of a School Project!</b></ListGroup.Item>
                            <ListGroup.Item variant="info"><b>Time:</b> 3 Months</ListGroup.Item>
                            <ListGroup.Item variant="info"><b>Language and Utilities:</b> Java, OpenStreetMap API, MapBox APIs</ListGroup.Item>
                        </ListGroup>
                        <br/>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg xl={7}>
                        <div className="text-center">
                            <Image width="100%" src="assets/images/huskymaps/thumbnail.jpg" rounded/>
                        </div>
                    </Col>
                </Row>
                <br/>
                <br/>
                <br/>
                <hr/>
            </Container>
            <Container className="justify-content-center">
                <div className="text-center">
                    <h2> Design and Outcome </h2>
                    <br/>
                </div>
                <Col lg={10}>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                                <h3> Overview </h3>
                        </Col>
                        <Col>
                            <p>
                                First, we start by analyzing the details of what the Husky Maps application needs.
                                As a visualization, the below image describes the overall steps we will take to build
                                the application.
                                <br/>
                                <br/>
                                <Image width="100%" src="assets/images/huskymaps/overview.jpg" rounded/>
                                <br/>
                                <br/>
                                First we need to start off with the auto-complete function, which is used to return suggestions
                                of possible matches of places that the user is currently trying to type.
                                Then, we use the information that we have from the MapBox and OpenStreetMap APIs to get a list
                                of points and directions out of that point to navigate through the map. Finally, we get the shortest path
                                between two points that the user has selected and display it on the App.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Autocomplete </h3>
                        </Col>
                        <Col>
                            <p>
                                The first part of our HuskyMaps app is the Autocomplete function.
                                Given a list of locations as well as their <em>weight</em> (how likely they are to be suggested),
                                predict how likely it is that the user is typing each query and present the user a list of the top-matching queries, in descending order of weight.
                                The way we do this efficiently is by having having a class that stores the country as a <code>String</code> as well as its
                                weight as a <code>long</code>, then doing a binary search over all the terms.
                                Since the weight can be extremely high, we choose to use <code>long</code> instead
                                of <code>int</code> to prevent Integer overflow.
                                <br/>
                                As a checkpoint, we also had a GUI to ensure its correctness as well as a sort of visualization for the end goal.
                                <br/>
                                <br/>
                                <Image width="100%" src="assets/images/huskymaps/autocompletegui.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> K-D Tree </h3>
                        </Col>
                        <Col>
                            <p>
                                A K-Dimensional Tree is essentially a binary search tree that uses a K-tiered approach in
                                determining the position of a Node in the tree. Since we do not have infinitely many points, if a user clicks on a point
                                on the map, we need to be able to know the closest point to the user's click.
                                <br/>
                                In the case of the HuskyMaps app, since we are dealing with 2-Dimensional data, (i.e points have X and Y coordinates),
                                we can efficiently store the points in a 2-Dimensional tree in order to do achieve a faster lookup time for a specific point or
                                a point closest to the users' input. The runtime of a binary search is <code>&theta;(logN)</code>. However,
                                the worst case scenario where a tree leans to one side would still yield <code>O(N)</code>.
                                <br/>
                                <Image width="50%" src="assets/images/huskymaps/kdtree.jpg" rounded/>
                                <Image width="50%" src="assets/images/huskymaps/kdtreevis.jpg" rounded/>
                                <br/>
                                As seen above, the relationship between Cartesian points with a 2-D Tree is preserved.
                                In some cases, we compare by the X-coordinates, whereas in others we compare by the Y-coordinates.
                                This ensures a smooth partition along some x and y coordinates, as shown in the diagram above.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Shortest Path </h3>
                        </Col>
                        <Col>
                            <p>
                                Now that we've got a working K-D Tree, we can find the shortest path between two points, since we can find the
                                nearest point to a user's click. There are many ways to find the shortest path, but the way that we will implement this
                                is by using Dijkstra's shortest-path algorithm. Since edges out of a point are not weighted, Dijkstra provides an optimized
                                point-to-point search between the start and ending nodes.
                                <br/>
                                <b>(NOTE: If the edges had non-negative weights, A star search would be better)</b> <br/>
                                Refer to the below slides for more information of Depth-First Search, which is essentially
                                the foundation of Dijkstra's algorithm. Our approach, however, assumes all weights are
                                equal to 0.
                                <iframe
                                    src="https://docs.google.com/presentation/d/e/2PACX-1vTbjk2KxMfDBBVR4Ts7KEELnth1kAS42R5tDB0H-eYGfoqpGPO2D5xVB5hoUHxTsyd_9z7wMfxXKO6J/embed?start=false&amp;loop=false&amp;delayms=3000"
                                    frameBorder="0" width="100%" height="434" allowFullScreen={true}/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Web Server </h3>
                        </Col>
                        <Col>
                            <p>
                                A large part of the web server has already been set up. The general idea of the web server
                                is that we handle Requests sent to us by the Web UI via a set of RouteHandlers that
                                handles requests based on its type. The user requests can include rastering, clearing routes, search for a given location, as well as
                                searching for a route between two points. We return the response using JSON via Google's GSON API.
                                <br/><br/>
                                The way we implement the raster request is by sending back a matrix representing
                                a "Tile" where each tile refers to a specific part of the map bounded by 2 Longitude and 2 Latitude
                                (i.e the topLeftX, topLeftY, botRightX, and botRightY coordinates).
                                <br/><br/>
                                Then we handle the search for points request by using our Autocomplete interface. We
                                try to match possible results from the user's query and return a list of possible matches to
                                it.
                                <br/><br/>
                                We handle finding path between two points by using the Dijkstra's interface that we implemented, finding
                                the nearest point of a user's click and then send it to the shortest path solver to find the nearest path
                                and returns it as a list of points to be rendered client-side.
                                <br/><br/>
                                Clear routes just returns an empty list of points to be rendered, thereby clearing the routes
                                that was already rendered.
                                <br/><br/>
                                Since a large part of this section was already implemented as part of the starter code,
                                this assignment focuses less on concepts, but more on understanding existing code and
                                integrating into it.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <div className="text-center">
                        <h2> Overall learning outcomes </h2>
                        <br/>
                    </div>
                    <Row>
                        <Col>
                            <Tabs defaultActiveKey="autocomplete" id="uncontrolled-tab">
                                <Tab eventKey="autocomplete" title="Autocomplete" style={{marginBottom: "12em"}}>
                                    <br/>
                                    <ListGroup>
                                        <ListGroup.Item variant="success">Formally introduce asymptotic runtime requirements and exception handling.</ListGroup.Item>
                                    </ListGroup>
                                </Tab>
                                <Tab eventKey="kdtree" title="K-D Tree" style={{marginBottom: "8em"}}>
                                    <br/>
                                    <ListGroup>
                                        <ListGroup.Item variant="success">Compose algorithms via iterative improvement.</ListGroup.Item>
                                        <ListGroup.Item variant="success">Encapsulate complex behavior beneath a simple interface.</ListGroup.Item>
                                    </ListGroup>
                                </Tab>
                                <Tab eventKey="shortestpath" title="Shortest Path" style={{marginBottom: "8em"}}>
                                    <br/>
                                    <ListGroup>
                                        <ListGroup.Item variant="success">Debug a graph algorithm involving multiple data structures.</ListGroup.Item>
                                        <ListGroup.Item variant="success">Apply the graph solver design pattern.</ListGroup.Item>
                                    </ListGroup>
                                </Tab>
                                <Tab eventKey="webserver" title="Web Server" style={{marginBottom: "5em"}}>
                                    <br/>
                                    <ListGroup>
                                        <ListGroup.Item variant="success">Extending a large, existing code base.</ListGroup.Item>
                                        <ListGroup.Item variant="success">Wrangling slightly messy real-world datasets into useful data structures.</ListGroup.Item>
                                        <ListGroup.Item variant="success">Implementing an algorithm based on closed-form expressions.</ListGroup.Item>
                                    </ListGroup>
                                </Tab>
                            </Tabs>
                        </Col>
                    </Row>
                </Col>
            </Container>
        </>
    );
}

export default HuskyMaps;