import React, {Component} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import NavigationBar from "./NavigationBar";
import About from './About';
import Home from './Home';
import Resume from './Resume';
import NotFound from "./NotFound";
import HuskyMaps from "./HuskyMaps";
import Pong from "./Pong";
import MiniSearchEngine from "./MiniSearchEngine";
import {Button} from "react-bootstrap";
import {Fade} from 'react-reveal';
import { animateScroll as scroll } from 'react-scroll'

class App extends Component<{}, {}> {

  render() {

    return (
        <>
            <NavigationBar/>
            <Router>
                <Switch>
                    <Route exact path="/" component={Home} />
                    <Route exact path="/home" component={Home} />
                    <Route exact path="/about" component={About} />
                    <Route exact path="/resume" component={Resume}/>
                    <Route exact path="/huskymaps" component={HuskyMaps} />
                    <Route exact path="/searchengine" component={MiniSearchEngine} />
                    <Route exact path="/pong" component={Pong} />
                    <Route component={NotFound} />
                </Switch>
            </Router>
            <br/>
            <br/>
            <Fade bottom>
                <div className="text-center">
                    <Button variant="outline-success" onClick={() => scroll.scrollTo(0)}> Back To Top </Button>
                </div>
            </Fade>
        </>
    );
  }
}

export default App;
