import React from 'react';
import {Col, Container, Image, ListGroup, Row} from "react-bootstrap";

function Pong() {
    return(
        <>
            <Container style={{marginTop: "5em"}}>
                <Row>
                    <Col lg xl={{ span: 4, offset: 1 }}>
                        <h1 style={{fontSize: "60px"}}> Pong </h1>
                        <ListGroup>
                            <ListGroup.Item variant="info"><b>Time:</b> 1 week</ListGroup.Item>
                            <ListGroup.Item variant="info"><b>Language and Utilities:</b> Java, Java Swing</ListGroup.Item>
                        </ListGroup>
                        <br/>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg xl={7}>
                        <div className="text-center">
                            <Image width="100%" src="assets/images/pong/pong.jpg" rounded/>
                        </div>
                    </Col>
                </Row>
                <br/>
                <br/>
                <br/>
                <hr/>
            </Container>
            <Container className="justify-content-center">
                <div className="text-center">
                    <h2> Design and Outcome </h2>
                    <br/>
                </div>
                <Col lg={10}>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Overview </h3>
                        </Col>
                        <Col>
                            <p>
                                Pong was the first experience I've had in Java GUI programming. The Pong game itself has
                                4 main components.
                                <ul>
                                    <li>Paddle</li>
                                    <li>Ball</li>
                                    <li>MainApp</li>
                                    <li>Renderer</li>
                                </ul>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Paddle </h3>
                        </Col>
                        <Col>
                            <p>
                                The paddle is rendered simply by filling in a black rectangle on a specified height/width.
                                Since the game consists of two players, the paddle is also identified by a paddleNumber member variable in its class declaration.
                                It also contains an <code>update()</code> function that takes in a boolean on whether it's up or down movement. This function will be called
                                on the main app class, where it will continue to render until stopped.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Ball </h3>
                        </Col>
                        <Col>
                            <p>
                                The ball is probably the most interesting part as it handles collision between paddles.
                                First off, since we need to calculate the angle at which it rebounds, the ball needs to know whether it's colliding
                                against the Player 1 paddle or the Player 2 paddle. Then upon impact, it calculates the angle at which it needs to rebound
                                by looking at the point of impact on the ball and on the paddle.
                                <br/><br/>
                                In addition, the ball also needs to know when to re-spawn or when it's past the bounds of the board. Upon doing so, it will
                                update the score of each player. Therefore, the player's score is located in the Ball class.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Main App </h3>
                        </Col>
                        <Col>
                            <p>
                                This is where the game starts and ends. First, we create a JFrame that will be the board. Then we render
                                the board (i.e set its color, fill the board) and add a <code>keyListener</code> object with the Main App component being the listener.
                                This allows us to listen for user inputs and update the state of the board accordingly (i.e move paddle up/down, set the score, etc.)

                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Renderer </h3>
                        </Col>
                        <Col>
                            <p>
                                The renderer class is essentially a wrapper class that extends JPanel. Its main function is to provide
                                the rendering capability of JPanel (mainly <code>paintComponent()</code>), and then passes it to
                                the Pong object to be used to render the parts of the game such as the paddle and the ball.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <div className="text-center">
                        <h2> Overall learning outcomes </h2>
                        <br/>
                    </div>
                    <Row>
                        <Col>
                            <ListGroup>
                                <ListGroup.Item variant="success">Basic understanding of Java's Swing library.</ListGroup.Item>
                            </ListGroup>
                        </Col>
                    </Row>
                </Col>
            </Container>
        </>
    );
}

export default Pong;