import React from 'react';
import {Carousel, Col, Container, Image, ListGroup, Row} from "react-bootstrap";

function MiniSearchEngine() {
    return(
        <>
            <Container style={{marginTop: "5em"}}>
                <Row>
                    <Col lg xl={{ span: 4, offset: 1 }}>
                        <h1 style={{fontSize: "60px"}}> Mini Search Engine </h1>
                        <ListGroup>
                            <ListGroup.Item variant="warning"><b>Part of a School Project!</b></ListGroup.Item>
                            <ListGroup.Item variant="info"><b>Time:</b> 3 Months</ListGroup.Item>
                            <ListGroup.Item variant="info"><b>Language and Utilities:</b> C/C++, pthreads, HTML</ListGroup.Item>
                        </ListGroup>
                        <br/>
                    </Col>
                    <Col xs={12} sm={12} md={12} lg xl={7}>
                        <Carousel interval={3000}>
                            <Carousel.Item>
                                <img
                                    width={600}
                                    height={500}
                                    className="d-block w-100"
                                    src="assets/images/searchengine/thumbnail.jpg"
                                    alt="first slide"
                                />
                            </Carousel.Item>
                            <Carousel.Item>
                                <img
                                    width={600}
                                    height={500}
                                    className="d-block w-100"
                                    src="assets/images/searchengine/333gle.jpg"
                                    alt="second slide"
                                />
                            </Carousel.Item>
                        </Carousel>
                    </Col>
                </Row>
                <br/>
                <br/>
                <br/>
                <hr/>
            </Container>
            <Container className="justify-content-center">
                <div className="text-center">
                    <h2> Design and Outcome </h2>
                    <br/>
                </div>
                <Col lg={10}>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Overview </h3>
                        </Col>
                        <Col>
                            <p>
                                In order to build a search engine, we need to understand how we want it to work first.
                                The way we can represent a search engine model, from a high level perspective, is to draw a diagram.
                                <Image width="100%" src="assets/images/searchengine/overview.jpg" rounded/>
                                <br/>
                                As seen from above, the main steps that we'll have is Indexing and Serving.
                                Since our search engine only searches from a list of pre-indexed websites as well as files
                                and directories that are not indexed, we would not need a web crawler, but we still need
                                file crawlers to find files and create indexes out of them. Then finally, after having a list of indexes,
                                we can easily find the list of matches of words and the documents that contain them. This is done largely by the
                                back-end server as well as the front-end UI.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> LinkedLists and HashTables </h3>
                        </Col>
                        <Col>
                            <p>
                                The first step in implementing a search-engine is figuring out what data structures we're gonna need. Since
                                this project's class is (somewhat) an introduction to both <b>C</b> and C++, the first part of this project
                                was done in the C-programming language.
                                <br/><br/>
                                In order for us to know where the word is located and how many times it is, we need to know the position of <b>all</b> the matching words
                                in a document. The data structure that comes to mind is a LinkedList. A LinkedList can store positions of a specific word in a document, thus
                                making it a great choice of data structure to use to keep track of word positions. However, we would soon run into another
                                issue of not knowing what the word actually is. This can be solved by using a HashTable, where the key of the Hash Table is the word and the value
                                is a LinkedList of the word positions. It would look a little like this
                                <br/>
                                <Image width="100%" src="assets/images/searchengine/linkedlist.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Creating Indices - File Parser </h3>
                        </Col>
                        <Col>
                            <p>
                                The next part of the problem involves creating indices for our files.
                                First we read the contents of a text file into memory and then parses the text file to look for words within it.
                                As it finds words, it will build up a HashTable that contains one record for each word.
                                Each record will contain a lowercase copy of the word, and also a sorted linked list.
                                Each record of the linked list contains an offset within the file that the word appeared in.
                                <br/>
                                <Image width="100%" src="assets/images/searchengine/fileparser.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Creating Indices - File Crawler </h3>
                        </Col>
                        <Col>
                            <p>
                                After figuring out the contents of the files, as well as the word position, we need to be able to
                                look for directories and files that we can pass down to the Indexer. In addition, we want to be able to
                                identify the documents by assigning a document ID to them. From then, we can build two HashTables
                                that maps a key of documentID to a value of documentName as well as another HashTable that maps
                                a hashed documentName to its documentID so that we can convert between them faster.
                                <Image width="100%" src="assets/images/searchengine/filecrawler.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Creating Indices - Indexer </h3>
                        </Col>
                        <Col>
                            <p>
                                The last component of creating indices that we need is the indexer. Its job is to take each
                                word from the HashTable produced by the Parser and then fold its contents into an inverted index.
                                An inverted index is just a hash table that maps from a word to a "posting list,"
                                which is a list of places that that word has been found. It would look something like this.
                                <Image width="100%" src="assets/images/searchengine/indexer.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Writing Indexes to Disk - Header </h3>
                        </Col>
                        <Col>
                            <p>
                                Keeping a search engine index in memory is problematic, since memory is expensive and also volatile.
                                Therefore, we need a way to store in on disk in a big-endian representation, since this is the
                                conventially used representation for portability. Generally, the layout will look something like the diagram
                                on the right.
                                <br/><br/>
                                The header consists of metadata about the index file. For example, it includes a "magic number" which is used to
                                identify the type of file it is (i.e format indicator). We always write the magic number at the last part, in case
                                of any failure, it's easy to tell if the index is corrupt or not.
                                <br/><br/>
                                The next part is the checksum area, this is a mathematical hash value that represents a signature of the index file.
                                This is used to check for corruption, since we can tell if the index is corrupt if the checksum in the header is not
                                equal to the checksum we've recalculated from disk.
                                <br/><br/>
                                The next four bytes store the size of the doctable region of the file. The size is stored as a 32-bit, signed, big endian integer.
                                <br/><br/>
                                The final four bytes of the header store the size of the index region of the file, in exactly the same way.
                            </p>
                        </Col>
                        <Col xs={2}>
                            <Image width="100%" src="assets/images/searchengine/diskindex.jpg" rounded/>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Writing Indexes to Disk - DocTable </h3>
                        </Col>
                        <Col>
                            <p>
                                The doctable is a hash table that stores a mapping from 64-bit document ID to an ASCII string representing the document's filename. This is the docid_to_docname HashTable that
                                was built up in the creation of the indices, just stored in Disk.
                                <br/><br/>
                                Generally this is how the doctable part of the inverted index would look like.
                                It consists of 3 main parts:
                                <br/>
                                The number of buckets inside the HashTable
                                <br/>
                                An array of <code>bucket_rec</code> records, which consists of two main pieces of information,
                                that is the number of elements in the bucket's chain (i.e <code>chain len</code>)
                                as well as the offset of the bucket within the index file (i.e <code>bucket position</code>)
                                <br/>
                                Finally it has the actual array of buckets. One bucket for each bucket in the HashTable.
                                <br/>
                                Each bucket, consists of an array of element positions. Since the elements
                                are variable sized, we need to know the offset they are in the index file.
                                <br/>
                                It also consists of an array of elements, each containing bits of information such as the
                                document ID, filename as well as the length of the filename.
                                <br/><br/>
                                <Image width="100%" src="assets/images/searchengine/doctable.jpg" rounded/>
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Writing Indexes to Disk - Index </h3>
                        </Col>
                        <Col>
                            <p>
                                The index is the most complicated of the three regions within the index file.
                                The great news is that it has pretty much the same structure as the doctable:
                                it is just a hash table, laid out exactly the same way.
                                The only part of the index that differs from the doctable is the structure of each element.
                                Refer to the pictures below for a visualization
                            </p>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={{span: 4, offset: 3}}>
                            <Image width="100%" src="assets/images/searchengine/index.jpg" rounded/>
                        </Col>
                        <Col sm={{span: 4}}>
                            <Image width="100%" src="assets/images/searchengine/indexdoctable.jpg" rounded/>
                        </Col>
                    </Row>
                    <hr/>
                    <Row>
                        <Col sm={{span: 3, offset: 3}}>
                            <h3> Search Engine Server </h3>
                        </Col>
                        <Col>
                            <p>
                                Our web server is a fairly straightforward multithreaded application. Every time a client connects to the server,
                                the server dispatches a thread to handle all interactions with that client.
                                Threads do not interact with each other at all, which greatly simplifies the design of the server.
                                <Image width="100%" src="assets/images/searchengine/threadarch.jpg" rounded/>
                                <br/>
                                Essentially, each thread would continue to interact with the Client by waiting and listening
                                for query requests and then the server will send a list of all matching documents with the query,
                                and when the client clicks on a file, the server accepts the <code>GET</code> request and sends them back
                                the file that the client selected.
                            </p>
                        </Col>
                    </Row>
                    <hr/>
                    <div className="text-center">
                        <h2> Overall learning outcomes </h2>
                        <br/>
                    </div>
                    <Row>
                        <Col>
                            <ListGroup>
                                <ListGroup.Item variant="success">Fundamentals of C/C++ as well as their libraries</ListGroup.Item>
                                <ListGroup.Item variant="success">Bug Journaling - Maintaining a list of occurred bugs to identify some common ones</ListGroup.Item>
                                <ListGroup.Item variant="success">Basic networking and multithreading using C pthreads</ListGroup.Item>
                            </ListGroup>
                        </Col>
                    </Row>
                </Col>
            </Container>
        </>
    );
}

export default MiniSearchEngine;